.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  position: relative; /* Tambahkan ini */
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
}

input {
  padding: 10px;
  font-size: 16px;
  margin: 10px;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #61dafb;
  border: none;
  color: #282c34;
  cursor: pointer;
  margin: 10px;
}

button:hover {
  background-color: #21a1f1;
}

.explosion-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.8); /* Transparansi latar belakang */
  z-index: 1; /* Bawa ke depan */
}

.explosion-image {
  width: 150%;
  height: 150%;
}
